body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.ipa {
    font-family: 'IPAPhon', 'Gentium Plus', sans-serif;
    font-size: 1.9em;
    margin: 10px;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.content {
    display: flex;
    flex: 1;
}

.sidebar {
    width: 250px;
    padding: 20px;
    background: #f5f5f5;
}

main {
    flex: 1;
    padding: 20px;
    overflow-y: auto;
}

.chapter-content {
    margin: 0 40px;
}

.main_content {
    display: flex;
}

.main_text {
    width: 60%;
    margin-right: 20px;
}

.main_img {
    width: 40%;
}

.navbar_text {
    width: 80%;
    margin-right: 20px;
    height: 200px;
}

.navbar_img_div {
    width: 20%;
}

.navbar_img {
    width: 200px;
}

.navbar {
    border-bottom: 2px #cccccc solid;
    position: relative;
    padding-left: 20px;
}

.navbar_buttons_div {
    position: absolute;
    bottom: 20px;
}

.navbar-button {
    margin-right: 20px;
    font-size: 16px;
    color: #25292e;
    background-color: #f6f8fa;
    box-shadow: 0px 1px 0px 0px #1f23280a;
    border-radius: .375rem;
    font-weight: 500;
    text-decoration: none;
    cursor: pointer;
    padding: 5px;
    border: 1px solid #25292e;
}

.navbar_title {
    font-size: 36px;
    font-weight: 400;
    margin-top: 40px;
}

.navbar_uni {
    font-size: 18px;
    font-weight: 400;
    margin-top: 20px;
}

.navbar_chair {
    font-size: 14px;
    font-weight: 400;
    margin-top: 5px;
}
